@import url('./TreeDark.css');
@media screen and (max-width: 1627px) {
  .tree__title {
    color: #000;
    margin: 0;
    padding: 0;
    margin-bottom: calc(0px + 4 * ((100vw - 320px) / 1308));
  }
  .tree__title_popup {
    text-align: center;
  }
  .tree__title_category_popup {
    color: #8960e2;
    text-align: center;
  }
  .tree__subtitle {
    color: #e62118;
    margin: 0;
    padding: 0;
    margin-bottom: calc(11px + 2 * ((100vw - 320px) / 1308));
  }
  .tree__subtitle_popup {
    text-align: center;
  }
  .tree__button {
    color: #fff;
    background: -o-linear-gradient(top, #441e78 0, #623d8d 100%);
    background: -webkit-gradient(
      linear,
      left top,
      left bottom,
      color-stop(0, #441e78),
      to(#623d8d)
    );
    background: linear-gradient(180deg, #441e78 0, #623d8d 100%);
    -webkit-box-shadow: 20px 20px 200px rgba(240, 240, 240, 0.48);
    box-shadow: 20px 20px 200px rgba(240, 240, 240, 0.48);
    border-radius: 10px;
    padding-right: 45px;
    padding-left: 45px;
    border: none;
    cursor: pointer;
    padding-top: calc(18px + 7 * ((100vw - 320px) / 1308));
    padding-bottom: calc(18px + 7 * ((100vw - 320px) / 1308));
  }
  
  
    .tree__popup {
      font-family: HelveticaNeueCyr, sans-serif;
      font-style: normal;
      letter-spacing: 0.05em;
      margin: 0 auto;
      background: #fcfcfc;
      border-radius: calc(10px + 10 * ((100vw - 320px) / 1308));
      width: calc(278px + 1063 * ((100vw - 320px) / 1308));
      padding-top: calc(40px + 30 * ((100vw - 320px) / 1308));
      padding-left: calc(19px + 65 * ((100vw - 320px) / 1308));
      padding-bottom: calc(40px + 30 * ((100vw - 320px) / 1308));
      padding-right: calc(19px + 65 * ((100vw - 320px) / 1308));
      position: relative;
    }
    .tree__popup-list {
      width: 100%;
      -webkit-box-pack: justify;
      -ms-flex-pack: justify;
      justify-content: space-between;
      display: -ms-grid;
      display: grid;
      /* grid-template-columns: repeat(2, minmax(240px, 1fr)); */
      grid-template-columns: repeat(1, minmax(240px, 1fr));
      -webkit-column-gap: calc(1px + 27 * ((100vw - 320px) / 1308));
      -moz-column-gap: calc(1px + 27 * ((100vw - 320px) / 1308));
      column-gap: calc(1px + 27 * ((100vw - 320px) / 1308));
      row-gap: calc(1px + 27 * ((100vw - 320px) / 1308));
    }
  
  }
.tree {
  font-family: HelveticaNeueCyr, sans-serif;
  font-style: normal;
  letter-spacing: 0.05em;
  font-weight: 500;
  font-size: calc(12px + 6 * ((100vw - 320px) / 1308));
  line-height: calc(18px + 9 * ((100vw - 320px) / 1308));
  
  width: 100%;
}
.tree__cards {
  width: 100%;
  display: -ms-grid;
  display: grid;
  grid-template-columns: repeat(3, minmax(280px, 365px));
  -webkit-box-pack: justify;
  -ms-flex-pack: justify;
  justify-content: space-between;

  -webkit-column-gap: 50px;
  -moz-column-gap: 50px;
  column-gap: 50px;
  row-gap: 25px;
  margin-bottom: 50px;
}
.tree__card {
  max-width: 365px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
 
}
.popup__container {
  padding-top: 35px;
}


@media screen and (max-width: 1627.98px) {
  .tree__cards {
    -webkit-column-gap: calc(0px + 50 * ((100vw - 320px) / 1308));
    -moz-column-gap: calc(0px + 50 * ((100vw - 320px) / 1308));
    column-gap: calc(0px + 50 * ((100vw - 320px) / 1308));
    /* row-gap: calc(0px + 50 * ((100vw - 320px) / 1308)); */
  }
}
@media screen and (max-width: 990px) {
  .tree__cards {
    grid-template-columns: repeat(2, minmax(280px, 365px));
    justify-content: center;
  }
}

@media screen and (max-width: 673px) {
  .tree__card {
    align-items: center;
  }
}

@media screen and (max-width: 630px) {
  .tree__cards {
    grid-template-columns: repeat(1, minmax(280px, 280px));
    /* justify-items: center; */
  }
}



@media screen and (max-width: 768px) {
  .tree__popup-list {
    /* grid-template-columns: 1fr; */
  }

}
