  background: #fcfcfc;
  -webkit-box-shadow: 20px 20px 200px rgba(240, 240, 240, 0.48);
  box-shadow: 20px 20px 200px rgba(240, 240, 240, 0.48);
  border-radius: 10px;
  padding-top: calc(12px + 27 * ((100vw - 320px) / 1308));
  padding-left: calc(18px + 22 * ((100vw - 320px) / 1308));
  padding-bottom: calc(12px + 27 * ((100vw - 320px) / 1308));
  padding-right: calc(18px + 22 * ((100vw - 320px) / 1308));
}
.tree__popup-title {
  font-weight: 500;
  font-size: calc(6px + 10 * ((100vw - 320px) / 1308));
  line-height: calc(9px + 15 * ((100vw - 320px) / 1308));
  color: rgba(0, 0, 0, 0.7);
  margin-bottom: calc(2px + 15 * ((100vw - 320px) / 1308));
}
.tree__popup-body {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-column-gap: calc(14px + -4 * ((100vw - 320px) / 1308));
  -moz-column-gap: calc(14px + -4 * ((100vw - 320px) / 1308));
  column-gap: calc(14px + -4 * ((100vw - 320px) / 1308));
}
.tree__popup-img {
  width: calc(45px + 29 * ((100vw - 320px) / 1308));
  height: calc(45px + 29 * ((100vw - 320px) / 1308));
  background: #ebebeb;
  border-radius: 10px;
}
.tree__popup-name {
  font-size: calc(10px + 14 * ((100vw - 320px) / 1308));
  line-height: calc(15px + 22 * ((100vw - 320px) / 1308));
  color: #000;
  text-transform: uppercase;
  margin: 0;
  padding: 0;
  margin-bottom: 1px;
}
.tree__popup-status {
  font-size: calc(6px + 8 * ((100vw - 320px) / 1308));
  line-height: calc(9px + 15 * ((100vw - 320px) / 1308));
  color: rgba(235, 0, 0, 0.7);
  margin: 0;
  padding: 0;
}
.tree__popup-user {
  font-size: calc(8px + 8 * ((100vw - 320px) / 1308));
  line-height: calc(15px + 22 * ((100vw - 320px) / 1308));
  color: rgba(14, 14, 14, 0.7);
  margin: 0;
  padding: 0;
  margin-bottom: 1px;
}
.tree__popup-partner {
  font-size: calc(8px + 6 * ((100vw - 320px) / 1308));
  line-height: calc(12px + 9 * ((100vw - 320px) / 1308));
  color: rgba(14, 14, 14, 0.7);
  margin: 0;
  padding: 0;
}
.tree__popup-partner-status {
  font-size: calc(6px + 8 * ((100vw - 320px) / 1308));
  line-height: calc(9px + 12 * ((100vw - 320px) / 1308));
}